const jsonLocalStorage = {
  setItem: (key, value) => {
    localStorage.setItem(key, JSON.stringify(value));
    // ì €ì¥ ì‹œ stringify : javascript ê°ì²´ or valueë¥¼ jsoní˜•íƒœë¡œ ë³€í™˜í•˜ì—¬ ì €ì¥
  },
  getItem: (key) => {
    return JSON.parse(localStorage.getItem(key));
    // êº¼ë‚¼ ë•Œ ë¬¸ìì—´ì„ javascript ê°ì²´ë¡œ ë³€í™˜
  },
};
const fetchCat = async (text) => {
  const OPEN_API_DOMAIN = "https://cataas.com";
  const response = await fetch(`${OPEN_API_DOMAIN}/cat/says/${text}?json=true`);
  const responseJson = await response.json();
  return `${OPEN_API_DOMAIN}/${responseJson.url}`;
};

const Form = ({ updateMainCat }) => {
  const [value, setValue] = React.useState('')
  const includesHangul = (text) => /[ã„±-ã…|ã…-ã…£|ê°€-í£]/i.test(text);
  const [errorMessage, setErrorMessage] = React.useState('')


  function handleInputChange(e) {
    const userValue = e.target.value
    setErrorMessage('')
    if (includesHangul(userValue)) {
      setErrorMessage('í•œê¸€ì€ ì“¸ ìˆ˜ ì—†ìŠµë‹ˆë‹¤.')
    }
    setValue(e.target.value.toUpperCase())
  }

  function handleFormSubmit(event) {
    event.preventDefault()
    setErrorMessage('')
    if (value === '') {
      setErrorMessage('ê°’ì„ ì…ë ¥í•´ì£¼ì„¸ìš”')
      return;
    }
    updateMainCat(value);
    setValue('')

  }
  // formì˜ ê¸°ë³¸ ë™ì‘ì€ ì œì¶œ í›„ refresh í•˜ëŠ” ê²ƒì„.
  return (
    <form onSubmit={handleFormSubmit}>
      <input
        type="text"
        name="name"
        placeholder="ì˜ì–´ ëŒ€ì‚¬ë¥¼ ì…ë ¥í•´ì£¼ì„¸ìš”"
        onChange={handleInputChange}
        value={value}
      />
      <button type="submit">ìƒì„±</button>
      {errorMessage === "" ? null :
        <p style={{ color: 'red' }}>{errorMessage}</p>}
    </form>
  )
}

