// í•˜ë‚˜ì˜ ë³€ìˆ˜ë¥¼ reactì—ì„œ elementë¼ê³  í•¨.


const App = () => {

  const [counter, setCounter] = React.useState(() =>
    jsonLocalStorage.getItem('counter')
  );
  // form ì»´í¬ë„ŒíŠ¸ ì•ˆì—ì„œ ì“°ì´ë˜ ìƒíƒœë¥¼ ìƒìœ„ì—ì„œ ê°™ì´ ì“°ê³  ì‹¶ì„ ë•Œ, ìƒíƒœë¥¼ ëŒì–´ì˜¬ë¦¬ê²Œ ë¨.
  // ë¶€ëª¨ ì»´í¬ë„ŒíŠ¸ì—ì„œ ìƒíƒœ ì„ ì–¸ í›„, ìì‹ ì»´í¬ë„ŒíŠ¸ì— propsë¡œ ë„˜ê²¨ì¤Œ. 
  // ìƒíƒœë¥¼ ì–´ë””ì— ë‘ëŠ”ì§€ëŠ” ë‹µì´ ì—†ìŒ. 

  async function setInitialCat() {
    const newCat = await fetchCat('Welcome to Cat-jjal maker')
    setMainCat(newCat)
  }
  const [mainCat, setMainCat] = React.useState(setInitialCat)
  const [favorites, setFavorites] = React.useState(() =>
    jsonLocalStorage.getItem('favorites') || [])

  const alreadyFavorite = favorites.includes(mainCat)


  // appì´ uië¥¼ ìƒˆë¡œ ê·¸ë¦´ë•Œë§ˆë‹¤ ì»´í¬ë„ŒíŠ¸ì˜ ì•ˆì˜ ëª¨ë“  ì½”ë“œë“¤ì´ í˜¸ì¶œë¨.
  // ì›í•˜ëŠ” ì‹œì ì—ë§Œ í˜¸ì¶œí•˜ê³  ì‹¶ì„ ë•Œ, useEffect() ì‚¬ìš©, ë°°ì—´ì„ ë„˜ê¸°ê³  ì›í•˜ëŠ” ìƒíƒœë¥¼ ë„˜ê²¨ì¤Œ.
  // 2ë²ˆì§¸ ì¸ìì¸ ë¹ˆ ë°°ì—´[]ì€ ì»´í¬ë„ŒíŠ¸ê°€ ë§¨ì²˜ìŒ ë‚˜íƒ€ë‚  ë•Œë§Œ ë¶ˆë¦¼. ë‹¤ë¥¸ ë³€ìˆ˜ë¥¼ ë„£ìœ¼ë©´ í•´ë‹¹ ë³€ìˆ˜ê°€ ë³€ê²½ë  ë•Œë§ˆë‹¤ í˜¸ì¶œë¨.
  React.useEffect(() => {
    setInitialCat()
  }, [])


  async function updateMainCat(text) {
    const newCat = await fetchCat(text)
    setMainCat(newCat)
    setCounter((prev) => {
      const nextCounter = prev + 1
      jsonLocalStorage.setItem('counter', nextCounter)
      return nextCounter
    })
  }
  function handleHeartClick() {
    const nextFavorites = [...favorites, mainCat]
    setFavorites(nextFavorites)
    jsonLocalStorage.setItem('favorites', nextFavorites)
  }

  const counterTitle = counter === null ? '' : counter + 'ë²ˆì§¸ '

  // í•¨ìˆ˜ ì´ë¦„ ì§“ê¸° ì»¨ë²¤ì…˜, handlerëŠ” handle ~ , ê·¼ë° propsë¡œ ë„˜ê¸°ë©´ on~
  return (
    <div>
      <Title> {counterTitle} ê³ ì–‘ì´ ê°€ë¼ì‚¬ëŒ€ </Title>
      <Form updateMainCat={updateMainCat} />
      <MainCard img={mainCat} onHeartClick={handleHeartClick} alreadyFavorite={alreadyFavorite} />
      <Favorites favorites={favorites} />
    </div>
  )
}

export default App;
